/* AdminGraph.css */
.AdminGraph {
  background-color: rgba(255, 255, 255, 0.565);
  border-radius: 25px;
  width: 70%;
  margin: auto;
}

.AdminGraph > button > a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 15px;
  font-weight: 600;
}

.AdminGraph > button > a:visited {
  color: rgb(255, 255, 255);
}

.AdminGraph > .glow-on-hover {
  margin: 10px;
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.AdminGraph > .glow-on-hover:before {
  content: "";
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.AdminGraph > .glow-on-hover:active:after {
  background: transparent;
}

.AdminGraph > .glow-on-hover:hover:before {
  opacity: 1;
}

.AdminGraph > .glow-on-hover:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000000;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
